export default {
	data() {
		return {
			list: {},
			pingjia: '',
			isAdd: true
		}
	},
	onLoad(option) {
		this.deId = option.id
		var _this = this
		this.$https({
			url: '/api/shop/mall-goods-detail',
			data: {
				goods_id: option.id
			},
			dengl: false,
			success: function(res) {
				_this.list = res.data.data.detail
				_this.pingjia = res.data.data.goodsComms
				console.log(res.data.data.detail)
			}
		})
	}
}
